// ========== About Page Dark Theme ========== //

.dark-theme {
  .about-page {
    // Hero section dark theme
    .about-hero-section {
      background: linear-gradient(135deg, rgba(255,255,255,0.08) 0%, rgba(255,255,255,0.03) 100%);
      
      &::before {
        background: radial-gradient(circle, rgba(147, 197, 253, 0.06) 0%, transparent 70%);
      }
    }
    
    // Avatar dark theme adjustments
    .avatar-wrapper {
      .about-avatar {
        box-shadow: 
          0 0 0 4px rgba(255, 255, 255, 0.1),
          0 0 30px rgba(147, 197, 253, 0.4),
          0 10px 40px rgba(0, 0, 0, 0.3);
        
        &:hover {
          box-shadow: 
            0 0 0 4px rgba(255, 255, 255, 0.2),
            0 0 40px rgba(147, 197, 253, 0.5),
            0 15px 50px rgba(0, 0, 0, 0.4);
        }
      }
      
      .avatar-border {
        background: conic-gradient(
          from 0deg,
          #93c5fd,
          #ec4899,
          #fbbf24,
          #34d399,
          #93c5fd
        );
        opacity: 0.8;
      }
    }
    
    // Floating tags dark theme
    .floating-tag {
      background: rgba(30, 30, 30, 0.9);
      color: #e5e5e5;
      border: 1px solid rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(15px);
      
      &:hover {
        background: rgba(40, 40, 40, 0.95);
        color: #fff;
      }
    }
    
    // Override individual tag colors for dark theme
    .tag-1 {
      background: linear-gradient(135deg, rgba(236, 72, 153, 0.8) 0%, rgba(251, 207, 232, 0.8) 100%);
      color: #fff;
    }
    
    .tag-2 {
      background: linear-gradient(135deg, rgba(34, 211, 238, 0.8) 0%, rgba(236, 72, 153, 0.8) 100%);
      color: #fff;
    }
    
    .tag-3 {
      background: linear-gradient(135deg, rgba(251, 191, 36, 0.8) 0%, rgba(245, 101, 101, 0.8) 100%);
      color: #fff;
    }
    
    .tag-4 {
      background: linear-gradient(135deg, rgba(52, 211, 153, 0.8) 0%, rgba(99, 102, 241, 0.8) 100%);
      color: #fff;
    }
    
    .tag-5 {
      background: linear-gradient(135deg, rgba(99, 102, 241, 0.8) 0%, rgba(139, 92, 246, 0.8) 100%);
      color: #fff;
    }
    
    .tag-6 {
      background: linear-gradient(135deg, rgba(244, 114, 182, 0.8) 0%, rgba(239, 68, 68, 0.8) 100%);
      color: #fff;
    }
    
    // Personal info dark theme
    .about-intro {
      .about-name {
        background: linear-gradient(135deg, #93c5fd 0%, #c084fc 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
      }
      
      .about-signature {
        color: #a1a1aa;
      }
    }
    
    // Content area dark theme
    .about-content {
      background-color: rgba(23, 23, 23, 0.95);
      color: #e5e5e5;
      border: 1px solid rgba(255, 255, 255, 0.1);
      
      h2, h3, h4, h5, h6 {
        color: #f5f5f5;
      }
      
      strong {
        color: #fff;
      }
      
      a {
        color: #93c5fd;
        
        &:hover {
          color: #ddd6fe;
        }
      }
      
      ul li {
        color: #d1d5db;
      }
    }
  }
}
